E7B;AACA;EACE,iBAAiB;EACjB,aAAa;EACb,UAAU;EACV,kBAAkB;EAClB,yBAAyB;AAC3B;AACA;EACE,sBAAsB;EACtB,aAAa;EACb,iBAAiB;EACjB,qBAAqB;EACrB,yBAAyB;AAC3B;AACA,+CAA+C,4BAA4B;AAC3E,iDAAiD,gBAAgB,CAAC;;AAElE;EACE;IACE,iBAAiB;EACnB;EACA;IACE,eAAe;EACjB;EACA;IACE,eAAe;EACjB;EACA;IACE,iBAAiB;EACnB;EACA;IACE,SAAS;EACX;EACA;IACE,8BAA8B;EAChC;;AAEF;;AAEA;EACE;IACE,0BAA0B;EAC5B;AACF;;AAEA;EACE;IACE,MAAM;EACR;AACF;;AAEA;;EAEE;IACE;;;8CAG+C;IAC/C,2BAA2B;IAC3B,sBAAsB;IACtB,WAAW;IACX,0BAA0B;EAC5B;EACA;IACE,kDAAwC;IACxC,2BAA2B;IAC3B,wBAAwB;IACxB,4BAA4B;IAC5B,kBAAkB;IAClB,iBAAiB;EACnB;EACA;IACE,kDAA0C;IAC1C,2BAA2B;IAC3B,yBAAyB;IACzB,4BAA4B;IAC5B,kBAAkB;IAClB,iBAAiB;EACnB;AACF","sourcesContent":["/*\n *  root settings\n*/\n:root {\n  --primary-color: #ffc93c;\n  --secondary-color: #393E46;\n  --third-color: #FFE6C7;\n  --content-color:#5b6068;\n  --icon-color: #ffff00;\n  --btn: #FF6000;\n  --btn-hover: #FFA559;\n\n\n  /* color */\n  --dark-color: #393E46;\n  --white-color: #ffffff;\n\n  /* Font */\n  --body-font: 'Poppins', sans-serif;\n  --nav-name-font-size: 1.5rem;\n  --normal-font-size: 1rem;\n  --header-height: 3rem;\n\n  /* Z-Index*/\n  --z-fixed: 80;\n}\n@media screen and (min-width: 768px) {\n  :root {\n    --nav-name-font-size: 1rem;\n    --normal-font-size: 1rem;\n  }\n}\n\n*, ::before, ::after {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\nul {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\na {\n  text-decoration: none;\n  display: inline-block;\n}\nh2 {\n  font-size: 2rem;\n  border-left: 4px solid var(--primary-color);\n  border-right: 4px solid var(--primary-color);\n  padding-left: 10px;\n}\n\n.bd-grid {\n  max-width: 1024px;\n  display: flex;\n  margin-left: 1.5rem;\n  margin-right: 1.5rem;\n}\n\nbody {\n  padding: 0;\n  width: 100%;\n  scroll-behavior: smooth;\n  margin: 0;\n  font-family: var(--body-font);\n  font-size: var(--normal-font-size);\n  background-color: var(--dark-color);\n}\n\n/* Skip link */\n.skip-link {\n  position: absolute;\n  top: -80px;\n  left: 0;\n  background-color: var(--btn);\n  color: var(--white-color);\n  padding: 8px;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n/* Button */\n\n.btn {\n  margin: 2em;\n  padding: 0.5em 1em;\n  font-weight: 700;\n  color: var(--white-color);\n  text-align: center;\n  vertical-align: middle;\n  background-color: var(--btn);\n  border: 1px solid transparent;\n  cursor: pointer;\n  text-decoration: none;\n  border-radius: 20px;\n  box-sizing: border-box;\n  min-width: 44px;\n  min-height: 44px;\n  font-size: 1.2em;\n}\n\n.btn:hover {\n  background-color: var(--btn-hover);\n}\nnav-bar {\n  width: 100%;\n}\n\n/* HEADER & NAV-BAR */\nheader {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 80px;\n  padding: 0 1rem;\n  background-color: var(--secondary-color);\n  z-index: var(--z-fixed);\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  box-shadow: 0 0.4px 2px rgba(255, 217, 121, 1);\n}\n\n.logo_header {\n  color: var(--white-color);\n  font-size:2.5rem;\n  font-weight: 600;\n}\n.logo_header span {\n  color:var(--primary-color);\n}\n.header__toggle {\n  font-size: 2.8rem;\n  cursor: pointer;\n  color: var(--dark-color);\n  box-sizing: border-box;\n  min-width: 44px;\n  min-height: 44px;\n}\n.navigasi__content {\n  display: flex;\n  flex-direction: column;\n}\n.navigasi__profile {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n  margin-bottom: 3rem;\n}\n.navigasi__img {\n  display: flex;\n  justify-content: center;\n  width: 60px;\n  height: 60px;\n  border-radius: 50%;\n  overflow: hidden;\n  margin-bottom: 1rem;\n}\n.navigasi__img img {\n  border:2px solid var(--primary-color);\n  border-radius: 50%;\n}\n.navigasi__name {\n  display: block;\n  font-size: var(--nav-name-font-size);\n  color:var(--white-color);\n  font-size: 2.5rem;\n  font-weight: bold;\n}\n.navigasi__name span { \n  color: var(--primary-color);\n}\n.navigasi__item {\n  margin-bottom: 2rem;\n  padding: 8px;\n}\n.navigasi__link {\n  color: var(--white-color);\n  font-size: 2em;\n  display: inline-block;\n  padding: 0.2em;\n}\n.navigasi__link:hover {\n  color: var(--primary-color);\n}\n\n/* show dan active */\n.show {\n  left: 0;\n}\n.active {\n  color: var(--primary-color);\n}\n\n\n/*\n * Hero\n */\n\n .hero {\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  text-align: center;\n  top: 50px;\n  position: relative;\n\n}\n\n.hero-img {\n  background:  linear-gradient(\n    rgba(160, 158, 136, 0.7),\n    rgba(181, 153, 26, 0.7)\n  ), url(/src/public/images/heros/hero-large.jpg);\n  background-position: center;\n  object-fit: cover;\n  min-height: 600px;\n  display: grid;\n  grid-template-columns: 1fr 2fr 1fr;\n}\n\n.hero__text {\n  align-self: center;\n  max-width: 600px;\n  margin: auto 0;\n  padding-bottom: 3em;\n  grid-column-start: 2;\n}\n\n.hero__title {\n  color: var(--primary-color);\n  font-weight: 600;\n  font-size: 35px;\n}\n\n.hero__sub_title {\n  color: var(--third-color);\n  margin: 16px;\n  margin-bottom: 30px;\n  font-size: 18px;\n  font-weight: 300;\n  word-spacing: 2px;\n  line-height: 1.36em;\n}\n\n\n\n/*\n * main\n */\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.main-content__container {\n  margin: 1em;\n  padding: 1em;\n  position: relative;\n}\n\n.main-content__title {\n  text-align: center;\n  margin-bottom: 1em;\n  color:var(--white-color);\n  margin-top: 2em;\n}\n\n#explore-restaurant {\n  display: grid;\n  grid-row-gap: 1.5em;\n  padding-top: 1.5em;\n}\n\n/*\n * Cards\n */\n\n.card {\n  width: 100%;\n  text-align: center;\n  transition: 0.3s;\n  cursor: pointer;\n  background-color: var(--content-color);\n  border-radius: 0.5em;\n}\n\n.card:hover {\n  box-shadow: 2px 2px 10px 0px var(--primary-color);\n}\n\n.card-rating{\n  position: absolute;\n  top: 0;\n  left: 0;\n  color: var(--white-color);\n  font-size: large;\n  background-color: var(--btn);\n  padding: 0.4em 0.5em;\n  z-index: 1;\n  border-radius: 0 0 0.5em 0;\n}\n .card-city {\n  top: 50px;\n  left: 0px;\n  z-index: 1;\n  position: absolute;\n  padding: 0.4em 0.5em;\n  color: rgb(232, 230, 227);\n  background-color:  var(--btn);\n  z-index: 1;\n  border-radius: 0 0.5em 0.5em 0;\n  font-size: medium;\n }\n\n.card-rating .fa, .card-city .fas {\n  font-size: smaller;\n  padding: 0.3em 0.8em 0.3em 0;\n  color: var(--icon-color);\n}\n\n.img-container {\n  width: 100%;\n  position: relative;\n  overflow: hidden;\n  transition: transform 0.5s ease;\n  max-height: 600px;\n  border-radius: 0.5em 0.5em 0 0;\n}\n\n.card-image {\n  object-fit: cover;\n  height: 100%;\n  width: 100%;\n}\n\n.card-content {\n  padding: 1.4em 2em;\n  font-size: small;\n  text-align: center;\n}\n\n.card-content-title {\n  font-weight: bold;\n  padding-bottom: 1em;\n  color: var(--primary-color);\n  font-size:var(--normal-font-size);\n}\n\n.card-content-title a {\n  color: var(--primary-color);\n  font-size: 1.2em;\n  padding: 0.6em;\n}\n\n.card-description {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  color:var(--white-color);\n}\n\n\n/* Extra small devices ( <= 320px ) */\n@media only screen and (max-width: 340px) {\n  nav {\n    display: flex;\n    flex-direction: row;\n  }\n\n  .logo-font,\n  .hero__title {\n    font-size: 1.5em;\n  }\n\n  .nav-item a,\n  .hero__sub_title,\n  .btn {\n    font-size: 1em;\n  }\n}\n\n/* Media Query - Responsive */\n@media screen and (min-width: 1024px) {\n  .bd-grid{\n    margin-left: auto;\n    margin-right: auto;\n  }\n}\n\n@media screen and (max-width: 768px) {\n  nav-bar {\n    width: 0;\n    position: fixed;\n  }\n  nav {\n    position: fixed;\n    top: 0;\n    left: -100%;\n    background-color: var(--dark-color);\n    width: 80%;\n    height: 100vh;\n    padding: 2rem 0;\n    z-index: var(--z-fixed);\n    transition: .5s;\n    overflow-y: auto;\n  }\n  .navigasi__name {\n    font-size: 2.4rem;\n\n  }\n  im